import './statics/styles/style.css'
import 'E:\\Desktop\\GitLocal\\mobius-ui\\src\\static.js'

import {
  initMobiusCSS,
  run, inDOM
} from 'MobiusUI'
import {
  whenContentLoaded,
  initMobiusJS, wxweb, initMpAPI,
  initRouter,
  initMpAuth
} from 'MobiusJS'
import { app } from './app.js'

whenContentLoaded(async () => {
  // turn CSS Houdini enhancements on
  initMobiusCSS()
  // init Mobius JS one-click start preset business
  await initMobiusJS({
    getAPITicketUrl: 'https://api.thoughtsdaily.cn/mp_api/',
    mpLoginUrl: 'https://api.thoughtsdaily.cn/mp_auth/',
    mpGetUserInfoUrl: 'https://api.thoughtsdaily.cn/mp_auth/'
  })
  // init WeChat Media Platform API Ticket
  initMpAPI({ appId: 'wx407c0931f62387fd' }).then(() => {
    wxweb.updateAppMessageShareData({
      title: '🤯汾阳本地通 - 专注本地便民服务', // 分享标题
      desc: '点击查看大家今天都发布了哪些信息，招聘求职、物品交易、失物招领、租房拼车、优惠活动...', // 分享描述
      link: 'http://app.thoughtsdaily.cn/board', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
      imgUrl: 'http://app.thoughtsdaily.cn/board/statics/favicons/thoughts-daily.icon.png', // 分享图标
      success: function () {
        // 设置成功
      }
    })
    wxweb.updateTimelineShareData({
      title: '🤯汾阳本地通 - 专注本地便民服务', // 分享标题
      link: 'https://app.thoughtsdaily.cn/board', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
      imgUrl: 'http://app.thoughtsdaily.cn/board/statics/favicons/thoughts-daily.icon.png', // 分享图标
      success: function () {
        // 设置成功
      }
    })
  })

  initRouter()

  initMpAuth({
    appId: 'wx407c0931f62387fd',
    preLogin: true,
    preGetUserInfo: true,
    scope: 'snsapi_userinfo'
  })

  run(app, inDOM('#mobius-app'))
})
